export default {
	data() {
		return {
			// title: 'Hello'
			love:[],
			timer:{}
		}
	},
	onLoad() {
		// this.back=uni.getBackgroundAudioManager();
		// //this.back.src="http://np01.sycdn.kuwo.cn/f9910277cdffb337a9736891722e725f/5d54fb4f/resource/n2/23/47/2110170044.mp3";
		// this.back.src="http://140.143.132.225/love/pdd.mp3";
		// this.back.title="音乐";
		// this.back.play()
	},
	onShow(){
		this.love=[];
		this.timer={};
		let msg={
			2000:"张雨不是个大逗逼",
			4000:"张雨不是个大逗逼",
			6000:"张雨不是个大逗逼",
			8000:"张雨不是个大逗逼",
			10000:"张雨不是个大逗逼",
			12000:"张雨不是个大逗逼",
			14000:"张雨不是个大逗逼",
			16000:"张雨不是个大逗逼",
			18000:"张雨不是个大逗逼",
			20000:"张雨不是个大逗逼",
			22000:"张雨不是个大逗逼",
			24000:"张雨不是个大逗逼",
			26000:"张雨不是个大逗逼",
			28000:"张雨不是个大逗逼",
			30000:"张雨不是个大逗逼"
		}
		let ref=this;
		for(let key in msg){
			let t =setTimeout(function(){
				ref.love.push(msg[key]);
				delete ref.timer[key];
			},key);
			ref.timer[key]=t;
		};
		
		this.back=uni.getBackgroundAudioManager();
		this.back.src="http://140.143.132.225/love/pdd.mp3";
		this.back.title="音乐";
		this.back.play()
	},
	onHide:function(){
		for(let key in this.timer){
			clearTimeout(this.timer[key])
		}
	},
	methods: {
		agree:function(){
			uni.showToast({
				icon:"none",
				title:"嗯，是的",
				duration:4000
			})
		},
		disagree:function(){
			uni.showModal({
				title:"再想想?",
				content:"真的么",
				cancelText:"拒绝",
				confirmText:"同意",
				success:function(res){
					if(res.confirm){
						uni.showToast({
							icon:"none",
							title:"哈哈哈",
							duration:3000
						})
					}else{
						uni.showToast({
							icon:"none",
							title:"是的",
							duration:3000
						})
					}
				}
			})
		}
	}
}
